  //    import ElMenuItem from "../../../node_modules/element-ui/packages/menu/src/menu-item";
  import store from '../../vuex/store'

  export default {
//        components: {ElMenuItem},
    data() {
      return {
        defaultActive: 'query',
        navBarItems: [
          {
            icon: 'el-icon-search',
            index: '/home/query',
            title: '查询会议',
          }, {
            icon: 'el-icon-menu',
            index: '/home/create',
            title: '添加会议',
          }, {
            icon: 'el-icon-setting',
            index: '/home/settings',
            title: '系统设置',
          },
        ]
      }
    },
    methods: {

      confirmLeave() {

        let _this = this;
        this.$confirm('是否退出系统?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {

          axios.post('/logout', {}).then((response) => {
            if (response.data.isLogout) {
              _this.$store.commit('DELETEUSER');
              _this.$router.push('/login');
            }
          }).catch((error) => {
            console.log(error);
          });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });

        })
      }
    },


  }



